@import "./src/common/style/sass/variables";
@import "./src/common/style/sass/mixins";

.infoContainer {
  @media (min-width: 992px) {
    display: flex;
  }

  .infoWrapper {
    @media (min-width: 992px) {
      flex: 0 0 50%
    }

    .infoList {
      padding: 0;
      flex-wrap: wrap;
      display: flex;
      @include text($textFontFamily, 14px, 1.6, capitalize, #fff);
      @media (min-width: 580px) {
        font-size: 15px;
      }

      .infoItem {
        display: flex;
        flex-direction: column;
        padding-bottom: 20px;
        flex: 0 0 50%;
        @media (min-width: 580px) {
          display: block;
        }

        .title {
          color: white;
          font-weight: 500;
          opacity: 0.8;
        }

        .value {
          text-transform: none;
          font-weight: 600;
        }
      }
    }
  }

  .links {
    @media (min-width: 992px) {
      flex: 0 0 50%;
    }


    ul {
      display: flex;
      color: #fff;
      list-style-type: none;
      justify-content: space-between;
      padding: 0;
      height: 100%;

      li {
        flex: 0 0 50%;
        max-width: 50%;
        height: 100%;

        a {
          width: 80%;
          padding: 6px;
          text-align: center;
          border-radius: 5px;
          margin: 40px auto;
          height: 50%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          text-decoration: none;
          color: white;
          transition: 0.3s;
          background: linear-gradient(100deg, #313543, transparent);
          box-shadow: 2px 2px 5px #13151a;

          &:hover {
            box-shadow: 0px 0px 10px $rootColor;
          }

          &:active {
            opacity: 0.7;
          }

          img {
            width: 50px;
          }

          p {
            font-size: 16px;
            @media (min-width: 992px) {
              font-size: 20px;
            }
          }
        }
      }
    }
  }
}
